
import axios from 'axios'

export default class DataService {
  smartlghDevices = process.env.REACT_APP_SMARTLGH_API_DEVICES;
  smartlghTelemetry = process.env.REACT_APP_SMARTLGH_API_TELEMETRY;
  
  constructor() {
    const p1 = process.env.REACT_APP_SMARTLGH_API_URI_PART1;
    const p2 = process.env.REACT_APP_SMARTLGH_API_URI_PART2;
    const p3 = process.env.REACT_APP_SMARTLGH_API_URI_PART3;
    const p4 = process.env.REACT_APP_SMARTLGH_API_URI_PART4;
    const p5 = process.env.REACT_APP_SMARTLGH_API_URI_PART5;
    this.smartlghBaseUrl = `${p1}${p2.split("").reverse().join("")}${p3}${p4}${p5}`;
  }

  fetchUsers = async () => {
    return new Promise((resolve, reject) => {
      console.log("[DataService.getUsers] Fetching users...")
      axios.get(`https://jsonplaceholder.typicode.com/users`)
        .then(res => {
          const persons = res.data;
          console.log("[DataService.getDevices] Successfully fetched users.");
          resolve(persons);
        })
        .catch(err => reject(err));
    });
  }

  fetchDevices = async () => {
    return new Promise((resolve, reject) => {
      console.log("[DataService.fetchDevices] Fetching devices...")
      axios.get(`${this.smartlghBaseUrl}/${this.smartlghDevices}`)
        .then(res => {
          const devices = res.data.Items;
          console.log("[DataService.getDevices] Successfully fetched devices.");
          resolve(devices);
        })
        .catch(err => {
          console.log("[DataService.getDevices]: Error fetching devices. ")
          console.log({ err });
          reject(err)
        });
    });
  }

  // { params: { startTime: Date.now()-86400000} })
  fetchTelemetry = async (deviceId, startTime, endTime, telemetryType) => {
    return new Promise((resolve, reject) => {
      console.log("[DataService.fetchDevices] Fetching device telemetry...")
      axios.get(`${this.smartlghBaseUrl}/${this.smartlghTelemetry}/${deviceId}`, 
      {params: { deviceid: deviceId, starttime: Date.now() - 604800000*2}})
        .then(res => {
          const telemetry = res.data;
          console.log("[DataService.fetchTelemetry] Successfully fetched telemetry.");
          resolve(telemetry);
        })
        .catch(err => {
          console.log("[DataService.fetchTelemetry]: Error fetching device telemetry. ")
          console.log({ err });
          reject(err)
        });
    });
  }

}