import * as React from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { useTheme } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import HomePage from '../pages/Home/HomePage'
import DevicesPage from '../pages/Devices/DevicesPage'
import TelemetryPage from '../pages/Telemetry/TelemetryPage'
import { useSelector, useDispatch } from 'react-redux';
import { selectCurrentPage, setCurrentPage } from '../features/layout/layoutSlice';

function TabPanel(props) {
  const { children, value, index, ...other } = props;


  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
      style={{ height: "100%" }}
    >
      {value === index && (

        // <Typography>{children}</Typography> 
        <Box sx={{ pb: 8, height: "100%" }}>{children}</Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}

export default function TabbedPageLayout() {
  const dispatch = useDispatch();
  const currentPage = useSelector(selectCurrentPage);
  const theme = useTheme();
  // const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    dispatch(setCurrentPage(newValue));
  };

  const handleChangeIndex = (index) => {
    dispatch(setCurrentPage(index));
  };

  return (
    <Paper elevation={6} sx={{ height: "calc(100% - 50px)", borderRadius: "15px", maxWidth: "900px", mx: "auto" }}>

    {/* // <Container disableGutters sx={{p: 0, height: "100%"}}> */}
      <AppBar position="absolute" color="primary" sx={{
        transform: "translateX(-50%)",
        left: '50%',
        top: 'auto',
        bottom: 30,
        px: 2,
        borderRadius: 33,
        maxWidth: '400px'
      }}>
        <Tabs
          value={currentPage}
          onChange={handleChange}
          indicatorColor="secondary"
          textColor="inherit"
          variant="fullWidth"
          aria-label="full width tabs example"
        >
          <Tab label="Home" {...a11yProps(0)} />
          <Tab label="Devices" {...a11yProps(1)} />
          <Tab label="Telemetry" {...a11yProps(2)} />
        </Tabs>
      </AppBar>
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={currentPage}
        onChangeIndex={handleChangeIndex}
        style={{ height: "100%" }}
      >
        <TabPanel value={currentPage} index={0} dir={theme.direction}>
          <HomePage />
        </TabPanel>
        <TabPanel value={currentPage} index={1} dir={theme.direction}>
          <DevicesPage />
        </TabPanel>
        <TabPanel value={currentPage} index={2} dir={theme.direction}>
          <TelemetryPage />
        </TabPanel>
      </SwipeableViews>
      {/* </Container> */}
    </Paper >
  );
}