export const TelemetryTable = (props) => {
    const { data } = props;
    const rows = data;
    return (
        <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }} aria-label="simple table">
                <TableHead>
                    <TableRow>
                        <TableCell>Device</TableCell>
                        <TableCell align="right">Value</TableCell>
                        <TableCell align="right">Time</TableCell>
                        <TableCell align="right">Type</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows && rows.length > 0 ?
                        rows.map((row, index) => (
                            <TableRow
                                key={index}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell component="th" scope="row">
                                    {row.Message.DeviceId}
                                </TableCell>
                                <TableCell align="right">{row.Message.TelemetryValue}</TableCell>
                                <TableCell align="right">{`${new Date(row.Timestamp).toLocaleString()}`}</TableCell>
                                <TableCell align="right">{row.Message.TelemetryType}</TableCell>
                            </TableRow>
                        ))
                        : (<TableRow
                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                        >
                            <TableCell component="th" scope="row">
                                No data.
                            </TableCell>

                        </TableRow>)}
                </TableBody>
            </Table>
        </TableContainer>
    );
}