import DataService from '../../services/dataService'

const _dataService = new DataService();

// A mock function to mimic making an async request for data
export async function fetchDevices() {
  console.log("DevicesApi.fetchDevices()");
  return await _dataService.fetchDevices();
}

export async function fetchTelemetry(deviceId) {
  console.log("DevicesApi.fetchTelemetry()");
  return await _dataService.fetchTelemetry(deviceId);
}

