import React, { useState } from 'react';
import { Container, Switch, Button, Paper, Box, Backdrop, CircularProgress, IconButton, Typography } from '@mui/material'
import { Settings } from '@mui/icons-material';

import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Notifications } from '../notifications/Notifications';
import { useSelector, useDispatch } from 'react-redux';
import { LightTheme, DarkTheme } from '../../app/themes';
import {
  selectLayout,
  selectCurrentPage,
  selectTheme,
  selectStatus,
  setTheme,
} from './layoutSlice';
import {
  addNotification,
  toggleShowNotifications,
} from '../notifications/notificationsSlice';
import NotificationList from '../../components/NotificationList'

import styles from './Layout.module.css';
import TabbedPageLayout from '../../layout/tabbedPageLayout';


export function MainLayout() {
  // const layout = useSelector(selectLayout);
  const currentPage = useSelector(selectCurrentPage);
  const theme = useSelector(selectTheme);
  const status = useSelector(selectStatus);
  const dispatch = useDispatch();

  const handleThemeSwitchChange = () => {
    dispatch(setTheme(theme === "light" ? "dark" : "light"));
  }

  const handleToggleShowNotifications = () => {
    dispatch(toggleShowNotifications());
  }

  const handleAddNotification = (type) => {
    const action = {
      notificationType: type,
      message: "Successfully added new notification.",
      icon: "",
    };
    dispatch(addNotification(action));
  }
  const themeSwitchChecked = theme === "dark";

  return (
    <ThemeProvider theme={theme === "light" ? LightTheme : DarkTheme}>
      <Container maxWidth disableGutters sx={{ position: 'fixed', height: '100%', bgcolor: 'body', pb: 3 }} >
        <Box sx={{ display: "flex", justifyContent: 'flex-end', p: 1, maxWidth: "900px", mx: "auto" }}>
          <Box>
          {/* <Typography variant="subtitle1" sx={{ color: "primary.contrastText", textAlign: "center" }}>Home Front</Typography></Box> */}
            <NotificationList></NotificationList>
            <IconButton
                aria-label="show notifications"
                color="primary"
                sx={{ fontSize: 40 }}
            >
            <Settings></Settings>
            </IconButton>
            <Switch label={"Dark mode"} checked={themeSwitchChecked} onChange={handleThemeSwitchChange} />
            {/* <Button onClick={() => handleAddNotification("normal")}>Add success</Button>
          <Button onClick={() => handleAddNotification("warning")}>Add warning</Button>
          <Button onClick={() => handleAddNotification("error")}>Add error</Button>
          <Button onClick={handleToggleShowNotifications}>Toggle</Button> */}
          </Box>
        </Box>
        {/* <Box maxWidthSm disableGutters sx={{height: "100%"}}> */}
        {/* <Paper rounded elevation={8} maxWidthSm sx={{ height: "100%", mx: "auto", maxWidth: 800 }}> */}
        <div style={{ position: "absolute" }}>
          <Backdrop open={status === "loading"} sx={{ color: '#444', zIndex: 1000, opacity: 0.5 }}
          >
            <CircularProgress color="inherit" />
          </Backdrop>
        </div>
          <TabbedPageLayout />
        {/* </Paper> */}
        {/* </Box > */}
        <Notifications />
      </Container>
    </ThemeProvider>
  );
}
