import { Avatar, Button, Badge, Box, Card, Chip, CardContent, Grid, Typography } from '@mui/material';
import { LocalFlorist, InsertChart, Circle, ArrowRight } from '@mui/icons-material';
import { TimeSince } from './TimeSince';
import { useSelector, useDispatch } from 'react-redux';
import {
  fetchUsersAsync,
  fetchDevicesAsync,
  fetchTelemetryAsync,
  setSelectedDeviceAsync,
} from '../features/devices/devicesSlice';

// import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
// import MoneyIcon from '@mui/icons-material/Money';

// Active: true
// BoardType: "ESP8266 NodeMCU, Soil moisture sensor"
// Created: 1612817193546
// Description: "Sensor at Rgv with Soil moisture sensor"
// DeviceId: "Rgv-Plant-Red"
// DeviceLastActive: 1612817193546
// DeviceType: "Sensor"
// ImageUrl: "images/device-board-blue.jpg"
// LastUpdated: 1612817193546
// Location: "Rgv"
// Name: "Rgv Plant Red soil moisture"
// SensorData: [{…}]
// ThingId: "ESP8266_Black"


export const Device = (props) => {
  const dispatch = useDispatch();
  const handleViewDeviceData = (deviceId) => {
    dispatch(fetchTelemetryAsync(deviceId));
  }
  const handleSetSelectedDeviceData = (deviceId) => {
    dispatch(setSelectedDeviceAsync(deviceId));
  }

  const { device } = props;
  // console.log({device});
  return (props && props.device ?
    <Card elevation={4}
      sx={{ height: '100%' }}
      {...props}
    >
      <CardContent>
        <Grid
          container
          // spacing={3}
          sx={{ justifyContent: 'space-between' }}
        >
          <Grid item
          >
            <Typography
              color="textSecondary"
              gutterBottom
              variant="overline"
            >
              {device.Location}
            </Typography>
          </Grid>
          <Grid item>


            {/* <Badge
              overlap="circular"
              anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
              variant="dot"
            >
              <Avatar
                sx={{
                  backgroundColor: 'primary.main',
                  height: 44,
                  width: 44
                }}
              >
                <LocalFlorist></LocalFlorist>
              </Avatar>
            </Badge> */}

            <Badge
              overlap="circular"
              anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
              badgeContent={
                <Avatar sx={{
                  border: 1,
                  borderColor: 'primary.main',
                  color: device.Active ? 'success' : 'error',
                  width: 12,
                  height: 12
                }}>
                  <Circle></Circle>
                </Avatar>
              }
            >
              <Avatar sx={{ backgroundColor: 'primary.main' }}><LocalFlorist></LocalFlorist></Avatar>
            </Badge>
          </Grid>
          <Grid item xs={12}>
            <Typography
              color="textPrimary"
              variant="h5"
            >
              {device.Name}

            </Typography>
          </Grid>
        </Grid>
        <Box
          sx={{
            pt: 2,
            display: 'flex',
            alignItems: 'center'
          }}
        >
          <Typography
            color="textSecondary"
            variant="subtitle2"
          >
            {device.Description}
          </Typography>
        </Box>
        <Box
          sx={{
            pt: 2,
            display: 'flex',
            alignItems: 'space-between'
          }}
        >
          <Grid container spacing={1} sx={{ justifyContent: 'space-between' }}>

            <Grid item>
              <Chip color="primary" icon={<LocalFlorist />} label={device.SensorData?.Moisture?.Value} variant="outlined" />

            </Grid>
            {/* <Grid item>
              <Chip color="primary" icon={<LocalFlorist />} label="22" variant="outlined" />

            </Grid>
            <Grid item>
              <Chip color="primary" icon={<LocalFlorist />} label="31%" variant="outlined" />

            </Grid> */}
            <Grid item xs={12}>
              <Typography
                color={"primary"}
                sx={{
                  mr: 1
                }}
                variant="subtitle2"
              >
                <TimeSince preText="Last updated" time={new Date(device.SensorData?.Moisture?.LastUpdated)} />
              </Typography>
            </Grid>
          </Grid>
        </Box>
        <Box xs={12} sx={{py: 2}}>
          <Button variant="contained" onClick={() => handleSetSelectedDeviceData(device.DeviceId)}>View device data<InsertChart></InsertChart></Button>
        </Box>
        {/* <Box xs={12}>
          <Button onClick={() => handleViewDeviceData(device.DeviceId)}>Load data</Button>
        </Box> */}
        <Box xs={12}
          sx={{
            pt: 2,
            display: 'flex',
            alignItems: 'center'
          }}
        >
          <Typography
            color="textSecondary"
            variant="caption"
          >
            {device.DeviceId}
          </Typography>
        </Box>
      </CardContent>
    </Card >
    :
    null
  )
};