import { Avatar, Box, Card, CardContent, Grid, Typography } from '@mui/material';
// import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
// import MoneyIcon from '@mui/icons-material/Money';

export const TimeSince = (props) => {
    const { time, preText } = props;

    if(!time) return null;

    const now = Date.now();
    const millisecondsSince = now - time.getTime();
    var timeframeWord = "days ";
    var sinceWord = "ago";
    var value = 5;

    const minutesSince = millisecondsSince / 60000;   
    const hoursSince = minutesSince / 60;
    const daysSince = hoursSince / 24;

    if (daysSince > 2) {
        timeframeWord = " days ";
        value = Math.floor(daysSince);
    }
    else if (daysSince > 1) {
        timeframeWord = "day ";
        value = "a ";
    }
    else if (hoursSince > 2) {
        timeframeWord = " hours ";
        value = Math.floor(hoursSince);
    }
    else if (hoursSince > 1) {
        timeframeWord = "hour ";
        value = "an ";
    }
    else if (minutesSince > 2) {
        timeframeWord = " minutes ";
        value = Math.floor(minutesSince);
    }
    else if (minutesSince > 1) {
        timeframeWord = "minute ";
        value = "a ";
    }
    else {
        timeframeWord = "";
        value = "just now";
        sinceWord = "";
    }

    return (
        <div>
            {`${preText} ${value}${timeframeWord}${sinceWord}`}
        </div>

    )
};
