import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
    selectDevices,
    selectStatus,
    selectUsers,
    selectTelemetry,
    fetchUsersAsync,
    fetchDevicesAsync,
    fetchTelemetryAsync,
} from '../../features/devices/devicesSlice';
import { Box, Container, Grid, Button, Backdrop, CircularProgress } from '@mui/material';
import { addNotification } from '../../features/notifications/notificationsSlice';
import { TelemetryTable } from '../../components/TelemetryTable';
import { TelemetryChart } from '../../components/TelemetryChart';
import { KeyboardArrowDown } from '@mui/icons-material';



export default function TelemetryPage() {
    const [showTable, setShowTable] = React.useState(false);
    const devices = useSelector(selectDevices);
    const status = useSelector(selectStatus);
    const users = useSelector(selectUsers);
    const telemetry = useSelector(selectTelemetry);
    const dispatch = useDispatch();

    const handleToggleShowTable = () => {
        setShowTable(!showTable);
    }

    return (
        <Box sx={{ p: 3 }}>
            <Box>
                <h1>Telemetry</h1>
                <TelemetryChart data={telemetry.Items}></TelemetryChart>
            </Box>
            <Box mt={4}>
                <Button onClick={() => handleToggleShowTable()}>{`${showTable ? "Hide" : "Show"} data table`}
                    <KeyboardArrowDown sx={{
                        transform: `${showTable ? "rotate(180)" : "rotate(0)"}`,
                        transition: "transform",
                        transitionDuration: 100,
                    }}></KeyboardArrowDown>
                </Button>
                {
                    showTable &&
                    <TelemetryTable data={telemetry.Items}></TelemetryTable>
                }
            </Box>
        </Box >
    )
}
