import React, { useState, Fragment } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Box, Container, Grid, Paper, Button, Popper, IconButton, Badge, Alert, Typography, Avatar, ListItem, List, ListItemAvatar, ListItemText } from '@mui/material';
import { CircleNotifications, Folder, Delete as DeleteIcon, Campaign, Error, TaskAlt } from '@mui/icons-material';
import { green, red, yellow } from '@mui/material/colors';
import { ThemeProvider } from '@mui/material/styles';
import { LightTheme, DarkTheme } from '../app/themes';

import { selectNotifications, removeNotification, clearNotifications } from '../features/notifications/notificationsSlice';

export default function NotifcationList() {
    const notifications = useSelector(selectNotifications);
    const notificationsCount = notifications.length;
    const arrowRef = React.createRef();
    const dispatch = useDispatch();


    const [anchorEl, setAnchorEl] = React.useState(null);
    const [open, setOpen] = React.useState(false);
    const [placement, setPlacement] = React.useState();

    const handleClick = (newPlacement) => (event) => {
        console.log("NotificaitonsIcon pressed.");
        setAnchorEl(event.currentTarget);
        setOpen((prev) => placement !== newPlacement || !prev);
        setPlacement(newPlacement);
    };

    const handleNotificationDelete = (notificationId) => {
        dispatch(removeNotification(notificationId));
    }

    const handleNotificationsClearAll = () => {
        dispatch(clearNotifications())
    }

    const greenShade = green[50];
    const redShade = red[50];
    const yellowShade = yellow[50];
    const greenShade2 = green[100];
    const redShade2 = red[100];
    const yellowShade2 = yellow[100];
    return (
        <Fragment>
            <IconButton ref={arrowRef}
                aria-label="show notifications"
                color="primary"
                sx={{ fontSize: 40 }}
                onClick={handleClick()}
            >
                <Badge badgeContent={notificationsCount} color="error">
                    <CircleNotifications />
                </Badge>
            </IconButton>
            <Popper open={open} anchorEl={anchorEl}>
                <ThemeProvider theme={LightTheme}>
                    <Box sx={{ border: 0, borderRadius: "5px", p: 1, boxShadow: 5, bgcolor: 'background.paper', minWidth: "400px" }}>
                        <Typography sx={{ p: 1 }} variant="h6" component="div">
                            Notifications
                        </Typography>
                        <Button onClick={() => handleNotificationsClearAll()}>Clear all</Button>
                        <List dense={true}>
                            {notifications && notifications.map(notification => (
                                <ListItem key={notification.id} sx={{
                                    borderRadius: "5px", my: 1,
                                    bgcolor: notification.notificationType === "normal" ? greenShade : notification.notificationType === "warning" ? yellowShade : redShade,
                                    border: 1,
                                    borderColor: notification.notificationType === "normal" ? greenShade2 : notification.notificationType === "warning" ? yellowShade2 : redShade2,
                                }}
                                    secondaryAction={
                                        <IconButton edge="end" aria-label="delete" onClick={() => handleNotificationDelete(notification.id)}>
                                            <DeleteIcon />
                                        </IconButton>
                                    }
                                >
                                    <ListItemAvatar>
                                        {/* <Avatar sx={{ bgcolor: "green" }}> */}
                                        <TaskAlt color="blue" />
                                        {/* </Avatar> */}
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary={notification.message}
                                        secondary={Math.floor((Date.now() - new Date(notification.createdAt).getTime()) / 60000) + " min ago"}
                                    />
                                </ListItem>
                            ))}
                        </List>
                        {/* <List dense={true}>
                    {notifications && notifications.map(notification => (
                        <ListItem
                            secondaryAction={
                                <IconButton edge="end" aria-label="delete">
                                    <DeleteIcon />
                                </IconButton>
                            }
                        >
                            <Alert severity="success" >{notification.message}</Alert>
                        </ListItem>
                    ))}
                </List> */}
                    </Box>
                </ThemeProvider>
            </Popper>
        </Fragment >
        //  </Box>
    );

}