import React, { Component, useState } from 'react';
import { Avatar, Box, Card, Alert, CardContent, Grid, Typography } from '@mui/material';
import Chart from "react-apexcharts";

export const TelemetryChart = (props) => {
    if (!props || !props.data || !props.data.length > 0) {
        return (
            <Box>
                <Alert>No data for device.</Alert>
            </Box>
        )
    }

    const data = props.data || [];
    const seriesName = data[0]?.ThingId || "unknown";
    var points = data.map(point => ({ x: new Date(point.Timestamp), y: (500 - (point.Message.TelemetryValue - 500)) / 5 }));
    const series = [{
        name: seriesName,
        data: points
    }
    ];

    const options = {
        chart: {
            height: 350,
            type: 'area'
        },
        dataLabels: {
            enabled: false
        },
        stroke: {
            curve: 'smooth'
        },
        xaxis: {
            type: 'datetime',
            categories: ["2018-09-19T00:00:00.000Z", "2018-09-19T01:30:00.000Z", "2018-09-19T02:30:00.000Z", "2018-09-19T03:30:00.000Z", "2018-09-19T04:30:00.000Z", "2018-09-19T05:30:00.000Z", "2018-09-19T06:30:00.000Z"]
        },
        yaxis: {
            max: 100,
            min: 0,
        },
        axisTicks: {
            show: true,
            borderType: 'solid',
            color: '#78909C',
            width: 6,
            offsetX: 0,
            offsetY: 0
        },
        tooltip: {
            x: {
                format: 'yyyy-MM-dd HH:mm'
            },
        },
    };

    return (
        <Box>
            <div id="chart" >
                <Chart options={options} series={series} type="area" height={350} />
            </div >
        </Box>

    );
}