import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  selectDevices,
  selectStatus,
  fetchDevicesAsync,
} from './devicesSlice';
import styles from './Devices.module.css';
import { Device } from '../../components/Device';
import { Box, Container, Grid, Button, Backdrop, CircularProgress } from '@mui/material';

export default function Devices() {
  const devices = useSelector(selectDevices);
  const status = useSelector(selectStatus);

  const dispatch = useDispatch();

  const handleFetchDevices = () => {
    dispatch(fetchDevicesAsync());
  }

  return (
    <Box sx={{ p: 3 }}>
      <h1>Devices ({devices.length})</h1>
      <Button variant="contained" onClick={() => handleFetchDevices()}>Load devices</Button>
      <Grid 
        container
        spacing={3}
        sx={{mt: 2}}
      >
        {devices && devices.map((device) => (
          <Grid key={device.DeviceId}
            item
            lg={6}
            sm={6}
            xl={3}
            xs={12}
          >
            <Device device={device} />
          </Grid>
        ))}
      </Grid>
      
    </Box>
  );
}
